export const headDefaultStyle = {
  'heading-1': H1,
  'heading-2': H2,
  'heading-3': H3,
  'heading-4': H4,
  'heading-5': H5,
  'heading-6': H6,
};

/**
 * TODO: 不知道什么情况下会有 h1/2/3/4，祖传代码暂时不改
 */
export function headTypeConvert(type: string) {
  let headType = type;
  if (/^h\d$/i.test(type)) {
    headType = `heading-${type.replace(/h/i, '')}`;
  }
  return headType;
}

export const isHeadingElement = (node: HTMLElement | null) => {
  const tagName = node?.tagName || '';
  return /^H\d$/.test(tagName);
};
